import React from 'react'
import '../css/projects.css'

export default function Projects({ projects }) {
    return (
        <div className="projectcont">
            {projects.map((project) => (
                <a className="project" key={ project.name } href={ project.link }>
                    <div className="text">
                        <h2>{ project.title }</h2>
                        <p>{ project.desc }</p>
                    </div>
                    <div className="image" style={ { backgroundImage: `url(${project.img})` } }></div>
                </a>
            )) }
        </div>
    )
}
